import React, { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import { getApp } from '../../../store/slices/app.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
}

const PrescriptionLensOverlayAnimation : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('prescription-lens-overlay-animation');
    const { assets } = useSelector(getApp);
    const imageRef = useRef<any>();
    let animationFrame: any = null;
    let imageIndex = 0;

    // HOOKS
    useEffect(() => {
        animationFrame = requestAnimationFrame(animate);

        return () => {
            cancelAnimationFrame(animationFrame);
        }
    }, [])
    

    // METHODS
    const animate = () => {
        animationFrame = requestAnimationFrame(animate);
        imageIndex += 1;
        if(imageIndex > 100){
            imageIndex = 0;
        } else {
            if(imageRef && imageRef.current){
                imageRef.current.src = assets[`avaOverlayClearLens${imageIndex}`].blob;
            }
        }
    }

    if(!assets)return null;

    return (
        <div className={B()}>
            <img src={assets[`avaOverlayClearLens0`].blob} alt="" ref={imageRef}/>
        </div>
    )
}

export default PrescriptionLensOverlayAnimation;