import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import measureSlice, { getMeasure } from '../../store/slices/measure.slice';
import useBEM from '../../utils/hooks/useBEM';
import useUtils from '../../utils/hooks/useUtils';
import MeasuresContainer from './components/MeasuresContainer';
import MeasuresData from './components/MeasuresData';
import measurementAnimation from './../../assets/videos/measurement.mp4';
import Spinner from '../../components/ui/Spinner';


interface Props {

}

const Measures : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('measures');
    const { view, step } = useSelector(getMeasure);
    const {downloadAsset} = useUtils();
    const [animationBlobData, setAnimationBlobData] = useState<string>();
    const animationRef = useRef<any>();  
    const timeoutDelays = useRef<any>([]);
    const dispatch = useDispatch();
    const [downloadProgress, setDownloadProgress] = useState<string>('0%');

    // HOOKS
    useEffect(() => {

        return () => {
            dispatch(measureSlice.actions.editPair(0));
        }
    }, [])

    useEffect(() => {
        if(view === 'data')return;
               
        const progressDownload = (percent: number) => {
            setDownloadProgress(`${percent}%`);
        }
        downloadAsset(measurementAnimation, progressDownload).then( blob => {            
            setAnimationBlobData(blob)
            animationRef.current.src = blob;
            animationRef.current.currentTime = 0;
            animationRef.current.play();

            addDelay(() => {
                animationRef.current.pause();
            }, 5500);

        }).catch(e => console.log(e));

        return () => {
            clearTimeouts();
            dispatch(measureSlice.actions.changeStep(0));
        }
    }, [view]);

    useEffect(() => {
        clearTimeouts();

        if(step === 0){            
            if(!animationRef.current)return;
            animationRef.current.currentTime = 0;
            animationRef.current.play();            
            addDelay(() => {
                animationRef.current.pause();
            }, 4500);
        }

        if(step === 1){
            if(!animationRef.current)return;
            animationRef.current.currentTime = 5.5;
            animationRef.current.play();            
            addDelay(() => {
                animationRef.current.pause();
            }, 5000);
        }

        if(step === 2){
            if(!animationRef.current)return;
            animationRef.current.currentTime = 10.5;
            animationRef.current.play();
            addDelay(() => {
                animationRef.current.pause();
            }, 3000);
        }

    }, [step])

    // METHODS
    const addDelay = (callback: any, delay: number) => {
        const timeout = setTimeout(() => {
            callback();
        }, delay);
        timeoutDelays.current.push(timeout);
    }

    const clearTimeouts = () => {
        timeoutDelays.current.forEach( (d:any) => clearTimeout(d) );
        timeoutDelays.current = [];
    }

    return (
        <div className={B(`step${step}`)}> 
            {
                !animationBlobData &&
                <Spinner text={downloadProgress} color={'rgb(0 134 214 / 33%)'} /> 
            }
            

            { animationBlobData && view === 'measure' &&
                <div className={E('animation', `step${step}`)}>
                    <video 
                            ref={animationRef}
                            playsInline
                            autoPlay={false} loop={false}  muted>                    
                            Your browser does not support the video tag.
                    </video>
                </div>
            }

            { view === 'measure' && animationBlobData && <MeasuresContainer/>}
            { view === 'data' && <MeasuresData/> }        
                        
        </div>
    )
}

export default Measures;